<template>
  <div class="container">
    <div class="checklist_left">
      <check-list @change="change_checked" />
    </div>
    <div class="checklist_right">
      <task-list :checked="checked" />
    </div>
  </div>
</template>

<script>
import checkList from "./components/checkList";
import taskList from "./components/taskList";

export default {
  components: {
    checkList,
    taskList
  },
  data() {
    return {
      checked: 0
    };
  },
  methods: {
    change_checked(checklist_id) {
      this.checked = checklist_id;
    }
  }
};
</script>

