<template>
  <div class="container">
    <div v-if="task_list.length">
      <!-- 待办事项卡片 -->
      <el-card
        class="box-card"
        shadow="hover"
        v-for="(item, index) in task_list_sort"
        :key="index"
      >
        <el-row :gutter="10">
          <el-col :span="1">
            <!-- 左侧图标 -->
            <div class="task-icon" @click="change_task_status(item)">
              <span v-if="item.status">
                <i class="el-icon-check" />
              </span>
              <span v-else>
                <i class="el-icon-s-flag" />
              </span>
            </div>
          </el-col>

          <!-- 待办事项标题 -->
          <el-col :span="item.task_time ? 15 : 20">
            <div :class="{ task_finished: item.status }">
              {{ item.title }}
            </div>
          </el-col>

          <!-- 待办事项时间 -->
          <el-col :span="item.task_time ? 5 : 0">
            <div class="task-time">
              {{ item.task_time }}
            </div>
          </el-col>

          <!-- 待办事项操作 -->
          <el-col :span="3">
            <div class="box-card__tool box-card__show">
              <i class="el-icon-more" @click="edit_task(item, index)" />
              <i class="el-icon-document-copy" @click="clip_task(item.title)" />
              <i class="el-icon-delete" @click="delete_task(item)" />
              <i
                class="el-icon-document-add"
                @click="to_today(task)"
                v-if="!item.add_time"
              />
            </div>
          </el-col>
        </el-row>
      </el-card>
    </div>
    <div v-else class="no-task">
      <i class="el-icon-info" />
      这里还什么都没有呢
    </div>

    <!-- 待办事项信息拟态框 -->
    <task-dialog
      :dialog="dialog"
      @dialogchange="dialogchange"
      :task-info="target_task"
      @taskinfochange="taskinfochange"
    />
  </div>
</template>

<script>
import { checklist_list_post } from "@/api/checklist";
import { addTask, changeTask, deleteTask, overdueToToday } from "@/api/task";
import taskDialog from "@/components/taskDialog";

export default {
  props: {
    checked: Number
  },
  components: {
    taskDialog
  },
  data() {
    return {
      task_list: [],
      dialog: false,
      dialog_status: true,
      target_task_index: 0,
      target_task: {
        title: "",
        content: "",
        cycle: "0",
        task_time: ""
      }
    };
  },
  watch: {
    checked() {
      this.fetchData();
    }
  },
  methods: {
    fetchData() {
      checklist_list_post(this.$store.getters.token, {
        checklist_id: this.checked
      }).then(response => {
        this.task_list = response.data;
      });
    },
    clip_task(text) {
      this.$copyText(text).then(() => {
        this.$notify({
          title: "系统通知",
          message: "复制待办事项成功",
          type: "success"
        });
      });
    },
    delete_task(task) {
      deleteTask(this.$store.getters.token, task.task_id).then(() => {
        for (let i = 0; i < this.task_list.length; i++) {
          if (this.task_list[i] === task) {
            this.task_list.splice(i, 1);
            this.$notify({
              title: "系统通知",
              message: "删除待办事项成功",
              type: "success"
            });
          }
        }
      });
    },
    change_task(task) {
      let data = {
        title: task.title,
        content: task.content,
        status: task.status,
        cycle: task.cycle,
        task_time: task.task_time
      };
      changeTask(this.$store.getters.token, task.task_id, data);
    },
    edit_task(task, index) {
      this.target_task = JSON.parse(JSON.stringify(task));
      this.target_task_index = index;
      this.dialog = true;
      this.dialog_status = false;
    },
    to_today(task) {
      overdueToToday(this.$store.getters.token, {
        task_id: task.task_id
      }).then(() => {
        this.fetch_data();
        this.$notify({
          title: "系统通知",
          message: "已添加到今日待办事项中",
          type: "success"
        });
      });
    },
    change_task_status(task) {
      this.task_list.forEach(item => {
        if (item === task) item.status = !item.status;
      });
      this.change_task(task);
      if (task.status) {
        this.$notify({
          title: "系统通知",
          message: "完成待办事项成功",
          type: "success"
        });
      } else {
        this.$notify({
          title: "系统通知",
          message: "取消待办事项完成",
          type: "success"
        });
      }
    },
    dialogchange(val) {
      this.dialog = val;

      this.target_task = {
        title: "",
        content: "",
        task_time: "",
        cycle: "0"
      };
    },
    taskinfochange(val) {
      this.task_list[this.target_task_index] = val;
      if (!this.dialog_status) {
        // 后端修改信息
        changeTask(this.$store.getters.token, val.task_id, val).then(() => {
          this.dialog_status = true;
          this.$notify({
            title: "系统通知",
            message: "修改成功",
            type: "success"
          });
          this.fetchData();
        });
      } else {
        addTask(this.$store.getters.token, val).then(() => {
          this.$notify({
            title: "系统通知",
            message: "添加成功",
            type: "success"
          });
        });
      }
    }
  },
  computed: {
    task_list_sort() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      return this.task_list.sort((m, n) => {
        if (m.status !== n.status) {
          return m.status - n.status;
        } else {
          return m.task_id - n.task_id;
        }
      });
    },
    task_list_count() {
      let new_task_list = this.task_list.filter(item => {
        return !item.status;
      });
      return new_task_list.length;
    }
  }
};
</script>

<style scoped>
.box-card {
  margin-bottom: 10px;
  min-width: 750px;
}

.box-card__tool {
  display: flex;
  justify-content: space-around;
}

.task-icon,
.box-card__tool i:hover {
  cursor: pointer;
}

.box-card__show {
  display: none;
}

.box-card:hover .box-card__show {
  display: flex;
}

.task_finished {
  text-decoration: line-through;
}
</style>
