<template>
  <div>
    <el-dialog
      :visible.sync="local_dialog"
      :title="title"
      @close="handle_close"
    >
      <el-form
        :model="local_checkItem"
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
      >
        <el-form-item label="清单名">
          <el-input
            type="text"
            v-model="local_checkItem.checklist_name"
            maxlength="10"
            show-word-limit
          />
        </el-form-item>
        <el-form-item label="颜色">
          <el-color-picker
            v-model="local_checkItem.checklist_color"
            :predefine="predefineColors"
          />
        </el-form-item>
        <el-form-item
          label="是否默认"
          v-show="local_checkItem.checklist_id !== default_checklist_id"
        >
          <el-switch v-model="local_checkItem.is_default" />
        </el-form-item>
        <el-form-item>
          <div
            style="display: flex;flex-direction: row;justify-content: flex-end;"
          >
            <el-button @click="handle_close">取消</el-button>
            <el-button type="primary" @click="handle_submit">
              {{ !status ? "新建" : "保存" }}
            </el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { checklist_options_post, checklist_options_put } from "@/api/checklist";

export default {
  props: {
    dialog: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    title: {
      type: String,
      default: function() {
        return "新建清单";
      }
    },
    checkItem: {
      type: Object,
      default: function() {
        return {
          checklist_name: "",
          checklist_color: "#409EFF",
          is_default: false
        };
      }
    },
    status: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    default_checklist_id: Number
  },
  watch: {
    dialog(val) {
      this.local_dialog = val;
    },
    checkItem(val) {
      this.local_checkItem = val;
    }
  },
  data() {
    return {
      local_dialog: this.dialog,
      local_checkItem: this.checkItem,
      rules: {},
      predefineColors: [
        "#ff4500",
        "#ff8c00",
        "#ffd700",
        "#90ee90",
        "#00ced1",
        "#1e90ff",
        "#c71585",
        "#c7158577"
      ]
    };
  },
  methods: {
    handle_close() {
      this.local_dialog = false;
      this.$emit("close", false);
    },
    handle_submit() {
      if (status) {
        checklist_options_post(
          this.$store.getters.token,
          this.local_checkItem
        ).then(response => {
          this.$emit("new", {
            ...this.local_checkItem,
            checklist_id: response.data.checklist_id
          });
          this.handle_close();
          this.$notify({
            title: "系统通知",
            message: "新建清单成功",
            type: "success"
          });
        });
      } else {
        checklist_options_put(
          this.$store.getters.token,
          this.local_checkItem
        ).then(() => {
          this.$emit("update", true);
          this.handle_close();
          this.$notify({
            title: "系统通知",
            message: "编辑清单成功",
            type: "success"
          });
        });
      }
    }
  }
};
</script>

<style scoped></style>
