<template>
  <div class="checklist-check-container">
    <div v-if="lists.length">
      <div
        class="checklist-check-item"
        :class="{ 'checklist-check-item__active': checked === item.value }"
        v-for="(item, index) in lists"
        :key="index"
        @click="change_checked(item.value)"
        :style="{ 'border-color': trans_border_color(item.value, item.color) }"
      >
        <div>
          {{ item.label }}
          <i
            class="el-icon-star-off"
            v-if="item.value === default_checklist_id"
          />
        </div>
        <div class="checklist-tool">
          <i class="el-icon-more" @click="edit_checklist(item)" />
        </div>
      </div>
    </div>
    <div v-else class="no-task">
      <i class="el-icon-info" />
      暂无清单
    </div>
    <div
      class="checklist-check-item"
      @click="new_checklist"
      style="display: block;cursor: pointer;"
    >
      <i class="el-icon-plus" />
      新建清单
    </div>

    <!--新建清单-->
    <check-list-dialog
      :dialog="dialog"
      :title="title"
      :status="status"
      :check-item="checkItem"
      :default_checklist_id="default_checklist_id"
      @close="check_list_dialog_close"
      @new="add_checklist"
      @update="update_checklist"
    />
  </div>
</template>

<script>
import { checklist_list_get } from "@/api/checklist";
import checkListDialog from "./checkListDialog";

export default {
  components: {
    checkListDialog
  },
  data() {
    return {
      lists: [],
      checked: 0,
      dialog: false,
      title: "新建清单",
      status: false,
      checkItem: {
        checklist_name: "",
        checklist_color: "#409EFF",
        is_default: false
      },
      default_checklist_id: 0
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      checklist_list_get(this.$store.getters.token).then(response => {
        this.lists = response.data;
        this.default_checklist_id = response.default_checklist_id;
        if (response.data.length) {
          this.checked = response.data[0].value;
          this.$emit("change", this.checked);
        }
      });
    },
    change_checked(checklist_id) {
      this.checked = checklist_id;
      this.$emit("change", checklist_id);
    },
    check_list_dialog_close() {
      this.dialog = false;
    },
    add_checklist(val) {
      this.lists.push({
        label: val.checklist_name,
        value: val.checklist_id,
        color: val.checklist_color
      });
    },
    update_checklist() {
      let checked = this.checked;
      this.fetchData();
      this.checked = checked;
    },
    trans_border_color(checklist_id, checklist_color) {
      if (checklist_id === this.checked) {
        return checklist_color;
      }
      return "#f2f2f2";
    },
    edit_checklist(checklistItem) {
      this.status = true;
      this.title = "编辑清单";
      this.checkItem = {
        checklist_name: checklistItem.label,
        checklist_color: checklistItem.color,
        checklist_id: checklistItem.value,
        is_default: checklistItem.is_default
      };
      this.dialog = true;
    },
    new_checklist() {
      this.status = false;
      this.title = "新建清单";
      this.checkItem = {
        checklist_name: "",
        checklist_color: "#409EFF",
        is_default: false
      };
      this.dialog = true;
    }
  }
};
</script>

<style scoped>
.checklist-check-container {
  padding: 30px 0 30px 30px;
  border: none;
  border-right: 1px solid #dcdfe6;
  min-height: 75vh;
}

.checklist-check-item {
  padding: 25px 10px 15px 20px;
  font-weight: 400;
  font-size: 16px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: none;
  border-right: 3px solid #f2f2f2;
}

.checklist-check-item:hover {
  background: #f2f6fc;
  border-color: #f2f6fc;
}

.checklist-check-item:hover .checklist-tool {
  display: block;
}

.checklist-check-item__active {
  border: none;
  border-right: 3px solid #409eff;
  background: #f2f6fc;
}

.checklist-tool {
  display: none;
  cursor: pointer;
}
</style>
